import React from 'react'

import classes from './style.module.css'

const Index = (props) => {
  const usersCloseHandler = () => {
    props.setToolBarProps('usersOpen',false)
  }
  console.log(props.toolBarProps);

  return (
    <div id='usersContainer' className={classes.container}>
      <h1>Participants</h1>
      <i
        className={`fas fa-times ${classes.closeChat}`}
        onClick={usersCloseHandler}
      ></i>
      <div className={classes.usersDiv} id='chatDivId'>
        {props.users.map((user) => {
          return (
            <div className={classes.user} key={user.name}>
              <p className={classes.name}>
                {user.name.length > 19
                  ? user.name.slice(0, 19) + '...'
                  : user.name}
              </p>
              <i
                className={`${classes.audio} ${
                  user.toolBarProps.audio
                    ? 'fas fa-microphone'
                    : 'fas fa-microphone-slash'
                } `}
              ></i>
              <i
                className={`${classes.video} ${
                  user.toolBarProps.video
                    ? 'fas fa-video'
                    : 'fas fa-video-slash'
                } `}
              ></i>
            </div>
          )
        })}
      </div>
    </div>
  )
}

export default Index
