import React from 'react'

import classes from './style.module.css'

const Index = (props) => {
  const { audio, video, chatOpen, usersOpen } = props.toolBarProps

  const showToolsHandler = (bool) => {
    if (bool) {
      document.getElementById('tools').style.opacity = '1'
    } else {
      document.getElementById('tools').style.opacity = '0.3'
    }
  }

  // const setToolBarProps = (item, bool) => {
  //   props.setToolBarProps((prevState) => {
  //     return {
  //       ...prevState,
  //       [item]: bool,
  //     }
  //   })
  // }

  const audioHandler = () => {
    if (!audio) {
      props.setToolBarProps('audio', true)
    } else {
      props.setToolBarProps('audio', false)
    }
  }
  const videoHandler = () => {
    if (!video) {
      props.setToolBarProps('video', true)
    } else {
      props.setToolBarProps('video', false)
    }
  }
  const chatOpenHandler = () => {
    if (!chatOpen) {
      props.setToolBarProps('chatOpen', true)
    } else {
      props.setToolBarProps('chatOpen', false)
    }
  }
  const usersOpenHandler = () => {
    if (!usersOpen) {
      props.setToolBarProps('usersOpen', true)
    } else {
      props.setToolBarProps('usersOpen', false)
    }
  }

  
  return (
    <div
      className={classes.container}
      onMouseOver={() => showToolsHandler(true)}
      onMouseOut={() => showToolsHandler(false)}
      style={{ zIndex: '2' }}
    >
      <div id='tools' className={classes.tools}>
        <i
          className={`fas ${audio ? 'fa-microphone' : 'fa-microphone-slash'}  ${
            classes.toolIcons
          }`}
          onClick={audioHandler}
        ></i>
        <i
          className={`fas ${video ? 'fa-video' : 'fa-video-slash'} ${
            classes.toolIcons
          }`}
          onClick={videoHandler}
        ></i>
        <i
          className={`fas fa-comments ${classes.toolIcons}`}
          onClick={chatOpenHandler}
        ></i>
        <i
          className={`fas fa-users ${classes.toolIcons}`}
          onClick={usersOpenHandler}
        ></i>
      </div>
    </div>
  )
}

export default Index
