import React,{useState} from 'react'

import VideoSmalls from './components/Video'
import ToolBar from './components/Toolbar'
import Chat from './components/Chat'
import Participants from './components/Participants'

function App() {

  const [socketId, setSocketId] = useState(1)
  
  const [refresh,setRefresh] = useState(false)

  const [toolBarProps, setToolBarProps] = useState({ audio: false, video: false, chatOpen: false, usersOpen: false })

  const [chatMessages, setChatMessages] = useState([])

  const [users, setUsers] = useState([
    {
      id: '1',
      name: 'ajay daniel trevor',
      toolBarProps: {
        audio: false,
        video: false,
        chatOpen: false,
        usersOpen: false,
      },
      setToolBarProps: function (item, bool) {
        this.toolBarProps[item] = bool
        setRefresh(prevState=> !prevState)
      },
      socketId: '1',
    },
    {
      id: '2',
      name: 'teena joanne trevor',
      toolBarProps: {
        audio: false,
        video: false,
        chatOpen: false,
        usersOpen: false,
      },
      setToolBarProps: function (item, bool) {
        this.toolBarProps[item] = bool
        setRefresh(!refresh)
      },
      socketId: '2',
    },
    {
      id: '3',
      name: 'anne santha mathew',
      toolBarProps: {
        audio: false,
        video: false,
        chatOpen: false,
        usersOpen: false,
      },
      setToolBarProps: function (item, bool) {
        this.toolBarProps[item] = bool
        setRefresh(!refresh)
      },
      socketId: '3',
    },
    {
      id: '4',
      name: 'trevor jyotish william',
      toolBarProps: {
        audio: false,
        video: false,
        chatOpen: false,
        usersOpen: false,
      },
      setToolBarProps: function (item, bool) {
        this.toolBarProps[item] = bool
        setRefresh(!refresh)
      },
      socketId: '4',
    },
  ])

  const currentUser = users.find(user=>user.socketId == socketId)

  return (
    <>
      <VideoSmalls users={users} />
      <ToolBar
        setToolBarProps={currentUser.setToolBarProps}
        toolBarProps={currentUser.toolBarProps}
      />
      {currentUser.toolBarProps.chatOpen ? (
        <Chat
          setToolBarProps={currentUser.setToolBarProps}
          toolBarProps={currentUser.toolBarProps}
          chats={chatMessages}
          setChat={setChatMessages}
          users={users}
        />
      ) : null}
      {currentUser.toolBarProps.usersOpen ? (
        <Participants
          setToolBarProps={currentUser.setToolBarProps}
          toolBarProps={currentUser.toolBarProps}
          users={users}
          setUsers={setUsers}
        />
      ) : null}
    </>
  )
}

export default App
