import React from 'react'

import classes from './style.module.css'

const Index = (props) => {
  const chatCloseHandler = () => {
    props.setToolBarProps('chatOpen', false)
  }

  const updateChatScroll = () => {
    setTimeout(() => {
      var elmnt = document.getElementById('chatDivId')
      elmnt.scrollTo({
        top: elmnt.scrollHeight,
        behavior:'smooth'
      })
      // elmnt.scrollTop = elmnt.scrollHeight
    },100)
  }
  updateChatScroll()

  const chatSubmitHandler = (e) => {
    e.preventDefault()
    e.persist()
    var message = e.target.chatInput.value
    var hours = new Date().getHours()
    if (hours > 12) {
      hours -= 12
    } else if (hours === 0) {
      hours = 12
    }
    var minute = new Date().getMinutes()
    if(minute < 10) minute = '0'+minute
    var time = hours + ':' + minute
    props.setChat((prevState) => {
      return [...prevState, { name: 'Ajay', msg: message, time: time }]
    })
    e.target.chatInput.value = ''
    e.target.chatInput.focus()
    updateChatScroll()
  }

  return (
    <div id='chatContainer' className={classes.container}>
      <h1>Chat</h1>
      <i
        className={`fas fa-times ${classes.closeChat}`}
        onClick={chatCloseHandler}
      ></i>
      <div className={classes.chatDiv} id='chatDivId'>
        
        {props.chats.map(chatM => {
          return (
            <div className={classes.chat} key={chatM.msg}>
              <p className={classes.name}>{chatM.name}</p>
              <pre className={classes.message}>
                {chatM.msg}
              </pre>
              <p className={classes.time}>{chatM.time}</p>
            </div>
          )
        })}
      </div>
      <div className={classes.chatFormDiv}>
        <form onSubmit={chatSubmitHandler} className={classes.chatForm}>
          <textarea
            type='text'
            name='chatInput'
            className={classes.chatInput}
            autoComplete='off'
            rows='1'
          />
          <button
            type='submit'
            style={{
              background: 'transparent',
              boxShadow: '0px 0px 0px transparent',
              border: '0px solid transparent',
              textShadow: '0px 0px 0px transparent',
              width:'20%'
            }}
          >
            <i
              className={`far fa-paper-plane ${classes.chatSubmitBtn}`}
              type='submit'
            ></i>
          </button>
        </form>
      </div>
    </div>
  )
}

export default Index
