import React, { useState,useEffect } from 'react'
import classes from './style.module.css'

const Index = (props) => {
  const [fullScreen, setFullScreen] = useState(false)

  const fullScreenHandler = (e, bool) => {
    
    var parent = e.target.parentElement
    var childIcon = parent.querySelector(`#i-${props.id}`)
    if (bool) {
      parent = e.target
    }
    if (!fullScreen) {
      parent.style.width = '200vh'
      parent.style.height = '100vh'
      childIcon.classList.remove('fa-expand')
      childIcon.classList.add('fa-compress')
      setFullScreen(true)
      const grantParent = parent.parentElement
      grantParent.insertBefore(parent, grantParent.childNodes[0])

    } else {
      parent.style.width = '28rem'
      parent.style.height = '21rem'
      childIcon.classList.add('fa-expand')
      childIcon.classList.remove('fa-compress')
      setFullScreen(false)
      const grantParent = parent.parentElement
      grantParent.insertBefore(parent, grantParent.childNodes[grantParent.childNodes.length])
    }
    window.scrollTo(0, 0)
  }

  useEffect(() => {
    if (
      /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        navigator.userAgent
      )
    ) {
      var touchtime = 0
      document.getElementById(props.id).addEventListener('click', function (e) {
        if (touchtime === 0) {
          touchtime = new Date().getTime()
        } else {
          if (new Date().getTime() - touchtime < 800) {
            fullScreenHandler(e, true)

            touchtime = 0
          } else {
            touchtime = new Date().getTime()
          }
        }
      })
    } else {
      console.log('not mobile device')
    }
  }, [props.id])
  
  
  
  return (
    <div
      id={props.id}
      className={classes.videoSmall}
      onDoubleClick={(e)=>fullScreenHandler(e,true)}
    >
      {/* ${classes.nameOnVideo} */}
      <h2 className={`${classes.nameOnNoVideo} `}>{props.name}</h2>
      <i
        id={`i-${props.id}`}
        onClick={fullScreenHandler}
        className={`fas fa-expand ${classes.fullScreenBtn}`}
      ></i>
    </div>
  )
}

export default Index
